export function getSparkleParticlesConfig() {
  return {
    key: sparkle,
    data: {
      lifespan: 600,
      frame: ['fx/fx_star.png'],
      scale: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.3, y: 1 },
          { x: 1, y: 0 }
        ]
      },
      alpha: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.3, y: 1 },
          { x: 1, y: 0 }
        ]
      },
      vy: { min: -2, max: 2 },
      vx: { min: -2, max: 2 },
      rotation: { delta: { min: -10, max: 10 } }
    }
  };
}

export const particles = [getSparkleParticlesConfig(), getLeftConfettiParticlesConfig()];

