import { lego } from '@armathai/lego';
import { ButtonCost } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { getViewByProperty, handScaleTw, searchAtlasByFrame } from '../../utils';

export class HintView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/hand.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.anchor.set(0.4, 0);

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    this._hide();
  }

  destroy() {
    this._removeTw();
    lego.event.off(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);

    super.destroy();
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  _show() {
    const { cash } = store.player;
    const { boardModel } = store.game;
    const { fromCellUuid, toCellUuid } = boardModel.getCellsForHint();

    if (cash < ButtonCost && (!fromCellUuid || !toCellUuid)) {
      lego.event.emit(ViewEvents.HintView.NothingToHint);
      return;
    }

    if (fromCellUuid && toCellUuid) {
      this._resetView();

      const boardView = getViewByProperty('name', 'BoardView');
      const fromCellGlobalPos = boardView.getCellPosition(fromCellUuid);
      const toCellGlobalPos = boardView.getCellPosition(toCellUuid);
      const fromCellLocalPos = this.parent.toLocal(fromCellGlobalPos, undefined);
      const toCellLocalPos = this.parent.toLocal(toCellGlobalPos, undefined);

      this.restartShowTweens(fromCellLocalPos, toCellLocalPos);
    } else if (cash >= ButtonCost) {
      const environmentView = getViewByProperty('name', 'EnvironmentView');
      const buttonGlobalPos = environmentView.getButtonPosition();
      const buttonLocalPos = this.parent.toLocal(buttonGlobalPos, undefined);

      this._firstTimeShow(buttonLocalPos);
    }
  }

  _resetView() {
    this.visible = true;
    this.alpha = 0;
    this.scale.set(0.7);
    this.angle = 0;
    this._removeTw();
  }

  restartShowTweens(from, to) {
    const { x: fromX, y: fromY } = from;
    const { x: toX, y: toY } = to;

    this.position.set(fromX, fromY);
    this._alphaUpTw = this.game.add.tween(this).to({ alpha: 1 }, 500, Phaser.Easing.Cubic.Out);
    this._scaleDownTw = this.game.add.tween(this.scale).to({ x: 0.5, y: 0.5 }, 500, Phaser.Easing.Cubic.Out);
    this._scaleUpTw = this.game.add.tween(this.scale).to({ x: 0.7, y: 0.7 }, 2000, Phaser.Easing.Cubic.Out);
    this._alphaDownTw = this.game.add.tween(this).to({ alpha: 0 }, 500, Phaser.Easing.Cubic.Out);
    this._moveTw = this.game.add.tween(this).to({ x: toX, y: toY }, 500, Phaser.Easing.Cubic.Out);

    this._alphaUpTw.start();
    this._scaleDownTw.start();
    this._scaleDownTw.chain(this._moveTw);
    this._moveTw.chain(this._scaleUpTw);

    this._scaleUpTw.onStart.addOnce(() => {
      this._alphaDownTw.start();
      this._alphaDownTw.onComplete.addOnce(() => {
        this._resetView();
        this.restartShowTweens(from, to);
      });
    });
  }

  _firstTimeShow(point) {
    this.scale.set(0.7);
    this.alpha = 1;
    this.position.set(point.x, point.y);
    this.angle = 0;
    this.visible = true;
    const { x, y, angle } = this;

    this.game.add
      .tween(this)
      .from({ x: x + 50, y: y + 50, angle: angle + 30 }, 400, Phaser.Easing.Cubic.Out, true, 0)
      .onComplete.addOnce(() => {
        this._pointHand();
      });
    this.game.add.tween(this).from({ alpha: 0 }, 400, Phaser.Easing.Cubic.Out, true, 0);
  }

  _pointHand() {
    this._handScaleTw = handScaleTw(this, 0.1, -1).repeatDelay(500);
  }

  _hide() {
    this._removeTw();
    this.visible = false;
  }

  _removeTw() {
    this.game.tweens.removeFrom(this.scale);
    this.game.tweens.removeFrom(this);

    this.game.tweens.remove(this._alphaUpTw);
    this.game.tweens.remove(this._scaleDownTw);
    this.game.tweens.remove(this._scaleUpTw);
    this.game.tweens.remove(this._alphaDownTw);
    this.game.tweens.remove(this._moveTw);
  }
}
