import { CellScale } from '@armathai/phaser2-grid';

export function getCTAStandardGridLandscapeConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'play_button',
        bounds: { x: 0.5, y: 0.65, width: 0.4, height: 0.2 }
      },
      {
        name: 'content',
        bounds: { x: 0, y: 0, width: 0.5, height: 1 }
      },
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'overlay',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}

