/* eslint-disable class-methods-use-this */
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getBackgroundGridConfig } from '../../configs/grid-configs';
import { getBoardBgTileImageConfig } from '../../configs/image-configs';
import { getGameBounds } from '../../utils';

export class BackgroundView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this._build();
  }

  get name() {
    return 'BackgroundView';
  }

  getGridConfig() {
    return getBackgroundGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    this._bg && this._rebuildBg();

    super.rebuild(config);
  }

  _build() {
    this.build(this.getGridConfig());

    this._buildBg();
  }

  _rebuildBg() {
    this._destroyBg();
    this._buildBg();
  }

  _destroyBg() {
    this._bg.destroy();
    this._bg = null;
  }

  _buildBg() {
    const { width, height } = getGameBounds();
    const { frame } = getBoardBgTileImageConfig();
    const bg = new Phaser.TileSprite(this.game, 0, 0, width, height, null, frame);
    bg.inputEnabled = true;
    bg.name = 'background';

    this.addChild((this._bg = bg));
  }
}
