import { lego } from '@armathai/lego';
import { CellState } from '../../constants';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { hideHintCommand } from '../ad/hint/hide-hint-command';
import { stopHintVisibilityTimerCommand } from '../ad/hint/stop-hint-visibility-timer-command';
import { disableRestCellsCommand } from '../board/disable-rest-cells-command';
import { setCellStateCommand } from '../board/set-cell-state-command';

export function onCellDragStartCommand(uuid) {
  lego.command
    //
    .payload(uuid, CellState.Drag)
    .execute(setCellStateCommand)

    .execute(disableRestCellsCommand)

    .guard(hintModelGuard)
    .execute(hideHintCommand, stopHintVisibilityTimerCommand);
}
