import { lego } from '@armathai/lego';
import { CashMultiplier } from '../../constants';
import { creditPlayerCashCommand } from '../player/credit-player-cash-command';

export function onTruckReachedPillarCommand(position, level) {
  lego.command
    //
    .payload(level * CashMultiplier)
    .execute(creditPlayerCashCommand);
}
