/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getUIGridConfig } from '../../configs/grid-configs';
import { getCashIconImageConfig } from '../../configs/image-configs';
import { getTopBarPatchConfig } from '../../configs/ninepatch-configs';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { hideToBottom, makeImage, makeNinePatch, showFromBottom } from '../../utils';
import { randomInt } from '../../utils/number/random-int';
import { CashView } from './cash-view';
import { PersistentCTAView } from './persistent-cta-view';
import { TimerView } from './timer-view';

export class UIView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    lego.event
      .on(ModelEvents.AdModel.PersistentCtaUpdate, this._onPersistentCtaUpdate, this)
      .on(ModelEvents.AdModel.TimerUpdate, this._onTimerUpdate, this)
      .on(ViewEvents.RoadView.TruckReachedPillar, this._onTruckReachedPillar, this);

    this._build();
  }

  get name() {
    return 'UIView';
  }

  getGridConfig() {
    return getUIGridConfig();
  }

  getTopBarPosition() {
    return this.toGlobal(this._topBar.position);
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  // BUILD
  _build() {
    this._buildTopBar();
    this._buildCash();
  }

  _buildTopBar() {
    this._topBar = makeNinePatch(getTopBarPatchConfig());
    this._topBar.inputEnabled = true;
    this._topBar.name = 'top_bar';

    this.setChild('top_bar', this._topBar);
  }

  _buildCash() {
    this._cash = new CashView();

    this.setChild('cash', this._cash);
  }

  // PERSISTENT
  _onPersistentCtaUpdate(persistentCta) {
    if (persistentCta) {
      this.game.tweens.remove(this._hideTw);
      this._hideTw = null;
      this._persistentCtaView && this._destroyPersistentCta();
      this._buildPersistentCta();
      this._showPersistentCta();
    } else {
      this.game.tweens.remove(this._showTw);
      this._showTw = null;
      this._hidePersistentCta().onComplete.add(this._destroyPersistentCta, this);
    }
  }

  _buildPersistentCta() {
    this.setChild('p_cta', (this._persistentCtaView = new PersistentCTAView()));
  }

  _destroyPersistentCta() {
    this._persistentCtaView.destroy();
    this._persistentCtaView = null;
  }

  _showPersistentCta() {
    const twArg = [this._persistentCtaView, 0, 500, false, Phaser.Easing.Cubic.Out];
    this._showTw = LP(showFromBottom, showFromBottom).call(null, ...twArg);
    return this._showTw;
  }

  _hidePersistentCta() {
    const twArg = [this._persistentCtaView, 0, 500, false, Phaser.Easing.Cubic.In];
    this._hideTw = LP(hideToBottom, hideToBottom).call(null, ...twArg);
    return this._hideTw;
  }

  // TIMER
  _onTimerUpdate(timer) {
    timer && this._buildTimer();
  }

  _buildTimer() {
    this._timer = new TimerView();

    this.setChild('timer', this._timer);
  }

  // COINS
  _onTruckReachedPillar(truckPosition) {
    this._throwCash(truckPosition);
  }

  _throwCash(truckPosition) {
    const count = 10;
    // const roadView = getViewByProperty('name', 'RoadView');
    const globalPos = truckPosition;
    const localPos = this.toLocal(globalPos, undefined);
    const destinationGlobalPos = this._cash.getCashPos();
    const destinationLocalPos = this.toLocal(destinationGlobalPos, undefined);

    for (let i = 0; i < count; i += 1) {
      const cash = makeImage(getCashIconImageConfig());
      const angle = randomInt(-180, 180);
      const xOffset = randomInt(-50, 50);
      const yOffset = randomInt(-50, 50);

      cash.angle = angle;
      cash.position.set(localPos.x + xOffset, localPos.y + yOffset);

      this.addChild(cash);

      this.game.add.tween(cash.scale).from({ x: 0, y: 0 }, 50, Phaser.Easing.Cubic.InOut, true, i * 50 - 50);

      const throwTw = this.game.add
        .tween(cash)
        .to(
          { x: destinationLocalPos.x, y: destinationLocalPos.y, angle: 0 },
          500,
          Phaser.Easing.Cubic.InOut,
          true,
          i * 50 - 50
        );

      throwTw.onComplete.add(() => {
        cash.destroy();
      });
    }
  }
}
