import { ButtonCost, FONT1 } from '../constants';

// TUTORIAL
export function getTutorialTextConfig(text) {
  return {
    text,
    style: {
      font: FONT1,
      fontSize: 46,
      fill: '#78483c',
      align: 'center',
      wordWrap: true,
      wordWrapWidth: 375
    }
  };
}

// PERSISTENT
export function getPersistentCtaTextConfig() {
  return {
    text: 'cta_btn_persistent_text',
    y: 1,
    style: {
      font: FONT1,
      fontSize: 32,
      fill: '#ffffff',
      stroke: '#000000',
      strokeThickness: 3
    }
  };
}

