export function getTopBarElementTextConfig(text) {
  return {
    text: {
      text: `${text}`,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    },
    style: {
      font: FONT1,
      fontSize: 42,
      fill: '#ffffff',
      stroke: '#000000',
      strokeThickness: 2
    }
  };
}

