import { lego } from '@armathai/lego';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { setHintDragActiveCommand } from './set-hint-drag-active-command';

export function onCellInputDownCommand() {
  lego.command
    .guard(hintModelGuard)
    .payload(true)
    .execute(setHintDragActiveCommand);
}
