import { store } from '../../models/store';

export function onCellTruckUpdateCommand(newValue, previousValue) {
  const { trucks } = store.game.boardModel;

  if (newValue) {
    store.game.boardModel.trucks = [...trucks, newValue];
  } else {
    const trucksUuids = trucks.map(t => t.uuid);
    const index = trucksUuids.indexOf(previousValue.uuid);
    index > -1 && trucks.splice(index, 1);
  }
}
