/* eslint-disable class-methods-use-this */
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getTutorialSequenceGridConfig } from '../../configs/grid-configs';
import { getCharacterImageConfig } from '../../configs/image-configs';
import { getBubblePatchConfig } from '../../configs/ninepatch-configs';
import { getTutorialTextConfig } from '../../configs/text-configs';
import { fitText, makeImage, makeNinePatch, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class TutorialSequenceView extends Phaser2Grid {
  constructor(config, index) {
    super(CI_API.game);

    this._showTween = null;
    this._hideTween = null;

    this._config = config;
    this._index = index;
    this.alpha = 0;

    this._build();
  }

  getGridConfig() {
    return getTutorialSequenceGridConfig();
  }

  destroy() {
    this.game.tweens.remove(this._showTween);
    this.game.tweens.remove(this._hideTween);

    super.destroy();
  }

  show() {
    this._showTween = this.game.add.tween(this).to({ alpha: 1 }, 300, Phaser.Easing.Linear.None, true);
    return this._showTween;
  }

  hide() {
    this._hideTween = this.game.add.tween(this).to({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true);
    return this._hideTween;
  }

  _build() {
    super.build(this.getGridConfig());
    this._buildContent();
    this._buildCharacter();
    this._buildBg();
    this._buildLabel();
  }

  _buildContent() {
    this._content = new Container();

    this.setChild(`${this._index}`, this._content);
  }

  _buildBg() {
    this._bg = makeNinePatch(getBubblePatchConfig());

    this._content.addChild(this._bg);
  }

  _buildCharacter() {
    this._character = makeImage(getCharacterImageConfig());
    this._character.scale.set(0.7);
    this._character.anchor.set(0.5, 1);
    this._character.position.set(0, -50);

    this._content.addChild(this._character);
  }

  _buildLabel() {
    const { width, height } = this._bg;
    const { text } = this._config;
    const label = makeText(getTutorialTextConfig(text));

    label.anchor.set(0.5);
    fitText(label, width * 0.8, height * 0.8);

    this._bg.addChild((this._label = label));
  }
}
