/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getRoadImageConfig } from '../../configs/image-configs';
import { AdStatus, TruckMovementSpeed } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { getGameBounds, getViewByProperty, loopRunnable, removeRunnable } from '../../utils';
import { difference } from '../../utils/array/difference';
import { Container } from '../../utils/container';
import { RoadTruckComponent } from './road-truck-component';

export class RoadView extends Container {
  constructor() {
    super();

    this._trucks = [];
    this._backlog = [];
    this._trucksOnRoad = [];

    this._build();

    lego.event
      .on(ModelEvents.BoardModel.TrucksUpdate, this._onTrucksUpdate, this)
      .on(ModelEvents.GameModel.TruckMovementUpdate, this._onTruckMovementUpdate, this)
      .on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  get name() {
    return 'RoadView';
  }

  getTruckByUuid(uuid) {
    return this._trucks.find(t => t.uuid === uuid);
  }

  getTruckPosition(uuid) {
    const truck = this._trucks.find(t => t.uuid === uuid);
    return this.toGlobal(truck.position);
  }

  removeFromBacklog(uuid) {
    const uuids = this._backlog.map(t => t.uuid);
    const i = uuids.indexOf(uuid);
    i > -1 && this._backlog.splice(i, 1);
  }

  getFromBacklog() {
    return this._backlog.shift();
  }

  addToBacklog(t) {
    this._backlog.push(t);
  }

  // REBUILD
  rebuild() {
    this._road && this._rebuildRoad();
    this._trucks && this._rebuildTrucks();
  }

  _removeMoveTweens() {
    this._trucks.forEach(truck => {
      this.game.tweens.remove(truck.moveTw);
    });
  }

  _rebuildRoad() {
    this._destroyRoad();
    this._buildRoad();
  }

  _rebuildTrucks() {
    this._trucks.forEach(truck => {
      this.bringToTop(truck);
    });
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        removeRunnable(this._poolRunnable);
        this._removeMoveTweens();
        break;
      default:
        break;
    }
  }

  _onTruckMovementUpdate(truckMovement) {
    truckMovement && this._startMoveTrucks();
  }

  _startMoveTrucks() {
    this._poolRunnable = loopRunnable(500, () => {
      const truck = this.getFromBacklog();
      const movingTrucks = this._trucksOnRoad.filter(t => t.moveTw);
      const onFirstTrack = movingTrucks.filter(t => t.track === 0);
      const onSecondTrack = movingTrucks.filter(t => t.track === 1);

      if (truck) {
        if (!onFirstTrack.length) {
          truck.track = 0;
          this._moveTruckToHouse(truck);
        } else if (!onSecondTrack.length) {
          truck.track = 1;
          this._moveTruckToHouse(truck);
        } else if (onFirstTrack.length && onSecondTrack.length) {
          this.addToBacklog(truck);
        }
      }
    });
  }

  _onTrucksUpdate(trucks) {
    this._buildTrucks(trucks);
    this._removeTrucks(trucks);
  }

  // TRUCKS
  _resetTruck(truck) {
    const { width } = getGameBounds();

    truck.alpha = 0;
    truck.hideVeggies();
    truck.position.set(-width / 2 - truck.width - 50, truck.track % 2 !== 0 ? -165 : -120);
  }

  _buildTrucks(trucks) {
    const trucksModels = trucks.map(f => f.uuid);
    const trucksView = this._trucks.map(f => f.uuid);

    const newTrucks = difference(trucksModels, trucksView);

    newTrucks.forEach(uuid => {
      const truck = new RoadTruckComponent(uuid);
      truck.scale.set(0.7);
      this._resetTruck(truck);
      this._trucks.push(truck);
      this._backlog.push(truck);
      this.addChild(truck);
    });
  }

  _removeTrucks(trucks) {
    const trucksModels = trucks.map(f => f.uuid);
    const trucksView = this._trucks.map(f => f.uuid);

    const removedTrucks = difference(trucksView, trucksModels);

    removedTrucks.forEach(uuid => {
      // Remove from all trucks
      const trucksUuids = this._trucks.map(t => t.uuid);
      const index = trucksUuids.indexOf(uuid);
      index > -1 && this._trucks.splice(index, 1);

      this.removeFromBacklog(uuid);
    });
  }

  // MOVEMENT
  _moveTruckToHouse(truck) {
    this._trucksOnRoad.push(truck);
    this._trucksOnRoad.forEach(t => t.track === 0 && this.bringToTop(t));

    const { x } = this._getHousePosition();
    const { level } = store.game.boardModel.getTruckByUuid(truck.uuid);
    const speed = TruckMovementSpeed[level];

    const tw = this.game.add.tween(truck).to({ x }, ((x - truck.x) / speed) * 1000, Phaser.Easing.Linear.None, true);
    tw.universal = true;
    truck.moveTw = tw;

    tw.onStart.add(() => {
      this._resetTruck(truck);
      truck.alpha = 1;
    });

    tw.onComplete.add(() => {
      this._moveTruckToPillar(truck, speed, level);
    });
  }

  _moveTruckToPillar(truck, speed, level) {
    const { x } = this._getPillarPosition();

    const tw = this.game.add
      .tween(truck)
      .to({ x: x - truck.width / 2 }, ((x - truck.x) / speed) * 1000, Phaser.Easing.Linear.None, true, 750);

    tw.universal = true;

    tw.onStart.add(() => {
      truck.takeVeggies();

      lego.event.emit(ViewEvents.RoadView.TruckTakeVeggies, level);
    });

    tw.onComplete.add(() => {
      this._moveTruckToFinish(truck, speed);

      lego.event.emit(ViewEvents.RoadView.TruckReachedPillar, this.toGlobal(truck.position), level);
    });
  }

  _moveTruckToFinish(truck, speed) {
    const { width } = getGameBounds();

    const tw = this.game.add
      .tween(truck)
      .to({ x: width }, ((width - truck.x) / speed) * 1000, Phaser.Easing.Linear.None, true, 0);
    tw.universal = true;

    tw.onComplete.add(() => {
      this.game.tweens.remove(truck.moveTw);
      truck.moveTw = null;

      const trucksUuids = this._trucksOnRoad.map(t => t.uuid);
      const index = trucksUuids.indexOf(truck.uuid);
      index > -1 && this._trucksOnRoad.splice(index, 1);

      if (this.getTruckByUuid(truck.uuid)) {
        this._resetTruck(truck);
        this.addToBacklog(truck);
      } else {
        this.removeChild(truck);
      }
    });
  }

  _getHousePosition() {
    const street = getViewByProperty('name', 'StreetComponent');
    const houseGlobalPos = street.getHousePosition();
    const houseLocalPos = this.toLocal(houseGlobalPos, undefined);

    return houseLocalPos;
  }

  _getPillarPosition() {
    const environment = getViewByProperty('name', 'EnvironmentView');
    const pillarGlobalPos = environment.getPillarPosition();
    const pillarLocalPos = this.toLocal(pillarGlobalPos, undefined);

    return pillarLocalPos;
  }

  // BUILD
  _build() {
    this._buildRoad();
  }

  _buildRoad() {
    const { width } = getGameBounds();
    const { frame } = getRoadImageConfig();
    const road = new Phaser.TileSprite(this.game, 0, 0, width * 2, 371, null, frame);
    road.anchor.set(0.5, 1);
    road.inputEnabled = true;
    road.name = 'road';

    this.addChild((this._road = road));
  }

  _destroyRoad() {
    this._road.destroy();
    this._road = null;
  }
}
