import { getRoadTruckImageConfig, getVeggiesImageConfig } from '../../configs/image-configs';
import { store } from '../../models/store';
import { makeImage } from '../../utils';
import { Container } from '../../utils/container';

export class RoadTruckComponent extends Container {
  constructor(uuid) {
    super();

    this._uuid = uuid;
    this._track = null;
    this._moveTw = null;

    this._build();
  }

  get uuid() {
    return this._uuid;
  }

  get track() {
    return this._track;
  }

  set track(value) {
    this._track = value;
  }

  get moveTw() {
    return this._moveTw;
  }

  set moveTw(value) {
    this._moveTw = value;
  }

  takeVeggies() {
    this._veggies.alpha = 1;
  }

  hideVeggies() {
    this._veggies.alpha = 0;
  }

  _build() {
    this._buildTruck();
    this._buildVeggies();
  }

  _buildTruck() {
    const { level } = store.game.boardModel.getTruckByUuid(this._uuid);
    this._truck = makeImage(getRoadTruckImageConfig(level));
    this._truck.inputEnabled = true;
    this._truck.name = 'road_truck';

    this.addChild(this._truck);
  }

  _buildVeggies() {
    this._veggies = makeImage(getVeggiesImageConfig());
    this._veggies.scale.set(0.7);
    this._veggies.position.set(-62.5, -25);
    this._veggies.alpha = 0;

    this.addChild(this._veggies);
  }
}
