import { ObservableModel } from '../../observable-model';

export class TruckModel extends ObservableModel {
  constructor(level) {
    super('TruckModel');

    this._level = level;

    this.makeObservable();
  }

  get level() {
    return this._level;
  }

  set level(value) {
    this._level = value;
  }
}
