import { lego, not } from '@armathai/lego';
import { CellState } from '../../constants';
import { setCellStateCommand } from '../board/set-cell-state-command';

export function onCellReturnTruckStartCommand(uuid, sameCell) {
  lego.command
    //
    .guard(not(() => sameCell))
    .payload(uuid, CellState.Return)
    .execute(setCellStateCommand)

    .guard(() => sameCell)
    .payload(uuid, CellState.Enable)
    .execute(setCellStateCommand);
}
