/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getBoardTruckImageConfig } from '../../../configs/image-configs';
import { ViewEvents } from '../../../events/view-events';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

export class CellView extends Container {
  constructor(uuid) {
    super();

    this._uuid = uuid;

    this._build();
  }

  get uuid() {
    return this._uuid;
  }

  getBounds() {
    return this._bg.getBounds();
  }

  getTruckDimensions() {
    return { position: this.toGlobal(this._truck.position), width: this._truck.width, height: this._truck.height };
  }

  buildTruck(level) {
    this._truck = makeImage(getBoardTruckImageConfig(level));

    this.addChild(this._truck);
  }

  destroyTruck() {
    this._truck && this._truck.destroy();
    this._truck = null;
  }

  enable() {
    this._truck.position.set(0, 0);
    this._truck.inputEnabled = true;
    this._truck.name = 'board_truck';
    this._truck.input.enableDrag();
    this._truck.events.onDragStart.add(this._onDragStart, this);
    this._truck.events.onDragStop.add(this._onDragStop, this);
    this._truck.events.onInputDown.add(this._onInputDown, this);
    this._truck.events.onInputUp.add(this._onInputUp, this);
  }

  disable() {
    this._truck.inputEnabled = false;
  }

  return() {
    this._truck.inputEnabled = false;

    this._truckReturnTw = this.game.add
      .tween(this._truck)
      .to({ x: 0, y: 0 }, 250, Phaser.Easing.Cubic.InOut, true, 0)
      .onComplete.add(() => {
        lego.event.emit(ViewEvents.CellView.ReturnTruckComplete, this._uuid);
      });
  }

  moveTo(x, y, toCellUuid) {
    const pos = this.toLocal(new Phaser.Point(x, y), undefined);

    this._truck.inputEnabled = false;

    this._truckReturnTw = this.game.add
      .tween(this._truck)
      .to({ x: pos.x, y: pos.y }, 250, Phaser.Easing.Cubic.InOut, true, 0)
      .onComplete.add(() => {
        lego.event.emit(ViewEvents.CellView.TruckMoveComplete, this._uuid, toCellUuid);
      });
  }

  // BUILD
  _build() {
    this._buildBg();
  }

  _buildBg() {
    const gr = this.game.add.graphics();
    gr.beginFill(0x0000ff, 0);
    gr.drawRoundedRect(-55, -55, 110, 110, 20);
    gr.endFill();

    this.addChild((this._bg = gr));
  }

  _onInputDown() {
    lego.event.emit(ViewEvents.CellView.InputDown);
  }

  _onInputUp() {
    lego.event.emit(ViewEvents.CellView.InputUp);
  }

  _onDragStart() {
    lego.event.emit(ViewEvents.CellView.DragStart, this._uuid);
  }

  _onDragStop(target, pointer) {
    const { tap_to_merge: tapToMerge } = CI_API.Globals.PARAMS;
    const {
      positionDown: { x: dX, y: dY },
      positionUp: { x: uX, y: uY }
    } = pointer;

    if (Math.abs(dX - uX) > 10 || Math.abs(dY - uY) > 10) {
      lego.event.emit(ViewEvents.CellView.DragStop, this._uuid);
    } else {
      tapToMerge
        ? lego.event.emit(ViewEvents.CellView.Click, this._uuid)
        : lego.event.emit(ViewEvents.CellView.DragStop, this._uuid);
    }
  }
}
