import { CellState } from '../../../constants';
import { ObservableModel } from '../../observable-model';

export class CellModel extends ObservableModel {
  constructor(row, column) {
    super('CellModel');

    this._row = row;
    this._column = column;
    this._truck = null;
    this._state = CellState.Unknown;

    this.makeObservable();
  }

  get row() {
    return this._row;
  }

  get column() {
    return this._column;
  }

  get truck() {
    return this._truck;
  }

  set truck(value) {
    this._truck = value;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  initialize() {
    this._state = CellState.Idle;
  }
}
