/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { InputPriority } from '../../constants';
import { ViewEvents } from '../../events/view-events';
import moduleBindings from '../../module-bindings';
import { hideToBottom, makePixel, pulse, showFromBottom } from '../../utils';
import { Button } from '../../utils/button/button';
import { CTAButtonComponent } from './cta-button-component';

export class CTAView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._blocker = null;
    this.bindings = moduleBindings;
  }

  getGridConfig() {
    return new Error('function getGridConfig() is not implemented');
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  build(config, blockerAlpha = 0) {
    super.build(config);
    this._buildBlocker(blockerAlpha);
    this._buildPlayButton();
  }

  show() {
    this._showBlocker();
    this._showPlayBtn();
  }

  // BUILD
  _buildPlayButton() {
    this._playBtn = new CTAButtonComponent();
    this._playBtn.alpha = 0;
    this.setChild('play_button', this._playBtn);
  }

  _buildBlocker(alpha) {
    this._blocker = makePixel({ alpha });
    this._blocker.inputEnabled = true;
    this._blocker.input.priorityID = InputPriority.Cta;
    this._blocker.name = 'cta_overlay';

    if (CI_API.Globals.PARAMS.cta_scrn_clickable) {
      this._blocker.events.onInputDown.add(this._onScreenClick, this);
    }

    this.setChild('blocker', this._blocker);
  }

  _buildButton(config, callback) {
    const btn = new Button(config);
    btn.onClick.add(callback, this);
    return btn;
  }

  // SHOW
  _showPlayBtn() {
    const twArg = [this._playBtn, 0, 500, false, Phaser.Easing.Cubic.Out];
    this._showButtonTw = LP(showFromBottom, showFromBottom).call(null, ...twArg);
    this._showButtonTw.onStart.add(() => {
      this._playBtn.alpha = 1;
    });

    this._showButtonTw.onComplete.add(() => {
      pulse(this._playBtn);
    });

    return this._showButtonTw;
  }

  _hidePlayBtn() {
    const twArg = [this._playBtn, 0, 500, false, Phaser.Easing.Cubic.In];
    this._hideButtonTw = LP(hideToBottom, hideToBottom).call(null, ...twArg);

    return this._showButtonTw;
  }

  _showBlocker() {
    this._showBlockerTw = this.game.add
      .tween(this._blocker)
      .to({ alpha: 0.8 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0);
    this._showBlockerTw.universal = true;

    return this._showBlockerTw;
  }

  _hideBlocker() {
    this._hideBlockerTw = this.game.add
      .tween(this._blocker)
      .to({ alpha: 0 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0);
    this._hideBlockerTw.universal = true;

    return this._hideBlockerTw;
  }

  // ON CLICK
  _onScreenClick() {
    lego.event.emit(ViewEvents.CtaView.ScreenClick);
  }
}
