import { CellScale } from '@armathai/phaser2-grid';

export function getForegroundGridLandscapeConfig() {
  return {
    // debug: { color: 0xd95027 },
    name: 'foreground',
    cells: [
      {
        name: 'logo',
        padding: 0.05,
        bounds: { x: 0.75, y: 0, width: 0.25, height: 0.25 }
      },
      {
        name: 'cta_logo',
        padding: 0.05,
        bounds: { x: 0.5, y: 0.15, width: 0.4, height: 0.5 }
      },
      {
        name: 'tutorial',
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'tutorial_blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}

