/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getBoardImageConfig } from '../../../configs/image-configs';
import { CellState, TruckMaxLevel } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';
import { CellView } from './cell-view';

export class BoardView extends Container {
  constructor() {
    super();

    this._cells = [];

    this._build();

    lego.event
      .on(ModelEvents.BoardModel.CellsUpdate, this._onCellsUpdate, this)
      .on(ModelEvents.CellModel.TruckUpdate, this._onCellTruckUpdate, this)
      .on(ModelEvents.CellModel.StateUpdate, this._onCellStateUpdate, this)
      .on(ViewEvents.CellView.DragStart, this._onDragStart, this)
      .on(ViewEvents.CellView.DragStop, this._onDragStop, this);
  }

  get name() {
    return 'BoardView';
  }

  getCellByUuid(uuid) {
    return this._cells.find(c => c.uuid === uuid);
  }

  getCellByPosition(row, column) {
    return this._cells.find(c => c.row === row && c.column === column);
  }

  getCellPosition(uuid) {
    return this.toGlobal(this.getCellByUuid(uuid).position);
  }

  // BUILD
  _build() {
    this._buildBoard();
  }

  _buildBoard() {
    this._board = makeImage(getBoardImageConfig());
    this._board.anchor.set(0.5, 1);
    this._board.inputEnabled = true;
    this._board.name = 'board';

    this.addChild(this._board);
  }

  // CELLS
  _onCellsUpdate(cells) {
    cells.forEach(cell => {
      const { row, column, uuid } = cell;
      const cellView = new CellView(uuid);
      const { width, height } = cellView;
      const gap = 14;

      cellView.position.set(
        -width - gap + (width + gap) * column,
        -this.height / 2 - height - gap + (height + gap) * row
      );

      this._cells.push(cellView);

      this.addChild(cellView);
    });
  }

  _onCellStateUpdate(state, previousValue, uuid) {
    const cell = this.getCellByUuid(uuid);

    switch (state) {
      case CellState.Enable:
        cell.enable();
        break;
      case CellState.Disable:
        cell.disable();
        break;
      case CellState.Return:
        cell.return();
        break;
      default:
        break;
    }

    if (state === CellState.ToCell) {
      const { uuid: toCellUuid } = cell;
      const { uuid: fromCellUuid } = store.game.boardModel.getCellByState(CellState.FromCell);
      const fromCell = this.getCellByUuid(fromCellUuid);
      const { x, y } = this.toGlobal(cell.position);

      fromCell.moveTo(x, y, toCellUuid);
    }
  }

  // TRUCK
  _onCellTruckUpdate(truck, previousValue, uuid) {
    const cell = this.getCellByUuid(uuid);
    const truckModel = store.game.boardModel.getCellByUuid(uuid).truck;

    truck ? cell.buildTruck(truckModel.level) : cell.destroyTruck();
  }

  // DRUG
  _onDragStart(uuid) {
    const cell = this.getCellByUuid(uuid);

    this.bringToTop(cell);
  }

  _onDragStop(uuid) {
    const cell = this.getCellByUuid(uuid);

    this._checkForIntersection(cell);
  }

  _checkForIntersection(cell) {
    const { position, width: tW, height: tH } = cell.getTruckDimensions();
    const truckLocalPosition = this.toLocal(position, null);
    const { x: tX, y: tY } = truckLocalPosition;
    const data = { volume: 0, uuid: null };

    this._cells.forEach(c => {
      const { x: cX, y: cY, width: cW, height: cH } = c;
      const truckBounds = new Phaser.Rectangle(tX - tW / 2, tY - tH / 2, tW, tH);
      const cellBounds = new Phaser.Rectangle(cX - cW / 2, cY - cH / 2, cW, cH);
      const { volume } = Phaser.Rectangle.intersection(truckBounds, cellBounds);

      if (volume > data.volume) {
        data.volume = volume;
        data.uuid = c.uuid;
      }
    });

    if (data.volume > 0) {
      const { truck: draggedTruck } = store.game.boardModel.getCellByUuid(cell.uuid);
      const { truck: intersectedTruck } = store.game.boardModel.getCellByUuid(data.uuid);

      if (!intersectedTruck) {
        // EMPTY CELL
        lego.event.emit(ViewEvents.CellView.MoveTruck, cell.uuid, data.uuid);
      } else if (intersectedTruck.level !== draggedTruck.level) {
        // LEVEL DO NOT MATCH
        lego.event.emit(ViewEvents.CellView.ReturnTruckStart, cell.uuid, false);
      } else if (cell.uuid === data.uuid) {
        // SAME CELL
        lego.event.emit(ViewEvents.CellView.ReturnTruckStart, cell.uuid, true);
      } else if (draggedTruck.level === TruckMaxLevel && intersectedTruck.level === TruckMaxLevel) {
        // INTERSECT BUT MAX LEVEL
        lego.event.emit(ViewEvents.CellView.ReturnTruckStart, cell.uuid, false);
      } else {
        // INTERSECT
        lego.event.emit(ViewEvents.CellView.MergeTrucks, cell.uuid, data.uuid);
      }
    } else {
      // OUT OF BOARD
      lego.event.emit(ViewEvents.CellView.ReturnTruckStart, cell.uuid, false);
    }
  }
}
