import { lego } from '@armathai/lego';
import { CellState } from '../../constants';
import { boardStateIdleGuard } from '../../guards/board/board-state-idle-guard';
import { TruckModel } from '../../models/game/board/truck-model';
import { store } from '../../models/store';
import { enableRestCellsCommand } from '../board/enable-rest-cells-command';
import { setCellStateCommand } from '../board/set-cell-state-command';
import { setCellTruckCommand } from '../board/set-cell-truck-command';
import { startGameCommand } from '../game/start-game-command';

export function mergeTrucksCommand(fromCellUuid, toCellUuid) {
  const { level } = store.game.boardModel.getCellByUuid(fromCellUuid).truck;

  lego.command
    //
    .payload(toCellUuid, null)
    .execute(setCellTruckCommand)

    .payload(fromCellUuid, null)
    .execute(setCellTruckCommand)

    .payload(fromCellUuid, CellState.Idle)
    .execute(setCellStateCommand)

    .payload(toCellUuid, CellState.Idle)
    .execute(setCellStateCommand)

    .payload(toCellUuid, new TruckModel(level + 1))
    .execute(setCellTruckCommand)

    .payload(toCellUuid, CellState.Enable)
    .execute(setCellStateCommand)

    .execute(enableRestCellsCommand)

    .guard(boardStateIdleGuard)
    .execute(startGameCommand);
}
