import { CellScale } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getUIGridLandscapeConfig() {
  const { timer } = CI_API.Globals.PARAMS;

  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'p_cta',
        padding: 0.1,
        bounds: { x: 0, y: 0.8, width: 0.325, height: 0.2 }
      },
      {
        name: 'top_bar',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 0.125 }
      },
      {
        name: 'timer',
        bounds: { x: 0.025, y: 0, width: 0.225, height: 0.125 }
      },
      {
        name: 'cash',
        bounds: { x: timer ? 0.25 : 0.025, y: 0, width: 0.225, height: 0.125 }
      }
    ]
  };
}

export function getUIGridPortraitConfig() {
  const { timer } = CI_API.Globals.PARAMS;

  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'p_cta',
        offset: { x: isSquareLikeScreen() ? 60 : 70, y: 0 },
        bounds: { x: 0, y: 0.85, width: 1, height: 0.15 }
      },
      {
        name: 'top_bar',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 0.125 }
      },
      {
        name: 'timer',
        bounds: { x: 0.05, y: 0, width: 0.3, height: 0.125 }
      },
      {
        name: 'cash',
        bounds: { x: timer ? 0.35 : 0.05, y: 0, width: 0.3, height: 0.125 }
      }
    ]
  };
}
