/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getTimerIconImageConfig } from '../../configs/image-configs';
import { getTopBarElementBgPatchConfig } from '../../configs/ninepatch-configs';
import { getTopBarElementTextConfig } from '../../configs/text-configs';
import { ModelEvents } from '../../events/model-events';
import { Container } from '../../utils/container';
import { fitText, makeImage, makeNinePatch, makeText, msToTime } from '../../utils/index';

export class TimerView extends Container {
  constructor() {
    super();

    lego.event
      .on(ModelEvents.TimerModel.RemainingUpdate, this._onTimerUpdate, this)
      .on(ModelEvents.TimerModel.StoppedUpdate, this._onTimerStoppedUpdate, this);

    this._build();
    this._updateText(CI_API.Globals.PARAMS.timer_amount);
  }

  get name() {
    return 'TimerView';
  }

  getBounds() {
    return this._bg.getBounds();
  }

  destroy() {
    lego.event
      .off(ModelEvents.TimerModel.RemainingUpdate, this._onTimerUpdate, this)
      .off(ModelEvents.TimerModel.StoppedUpdate, this._onTimerStoppedUpdate, this);

    super.destroy();
  }

  _updateText(value) {
    const time = msToTime(value);
    const text =
      parseInt(time.secondsStr, 10) >= 10 ? `${parseInt(time.secondsStr, 10)}` : `${parseInt(time.secondsStr, 10)}`;

    this._text.setText({
      text,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    });
  }

  _onTimerStoppedUpdate(stopped) {
    if (stopped) {
      // this._hide();
    }
  }

  _onTimerUpdate(newValue) {
    this._updateText(newValue);
  }

  _build() {
    this._buildBg();
    this._buildText();
    this._buildTimerIcon();
  }

  _buildBg() {
    this._bg = makeNinePatch(getTopBarElementBgPatchConfig());
    this.addChild(this._bg);
  }

  _buildText() {
    const { width, height } = this._bg;

    this._text = makeText(getTopBarElementTextConfig(0));
    this._text.anchor.set(0.5);

    fitText(this._text, width * 0.6, height * 0.8);

    this.addChild(this._text);
  }

  _buildTimerIcon() {
    this._cashIcon = makeImage(getTimerIconImageConfig());
    this._cashIcon.position.set(this.left, 0);

    this.addChild(this._cashIcon);
  }
}
