import { getBackgroundItemsConfig } from '../../configs/background-items-config';
import { getItemImageConfig, getPumpkinImageConfig } from '../../configs/image-configs';
import { getPumpkinsConfig } from '../../configs/pumpkins-config';
import { makeImage } from '../../utils';
import { Container } from '../../utils/container';

export class BackgroundItemsComponent extends Container {
  constructor() {
    super();

    this._items = [];
    this._pumpkins = [];

    this._build();
  }

  reposition() {
    this._repositionItems();
    CI_API.Globals.PARAMS.halloween_theme && this._repositionPumpkins();
  }

  _repositionItems() {
    const itemsConfig = getBackgroundItemsConfig();
    itemsConfig.forEach((config, i) => {
      const { x, y, scale } = config;
      this._items[i].position.set(x, y);
      this._items[i].scale.set(scale);
    });
  }

  _repositionPumpkins() {
    const itemsConfig = getPumpkinsConfig();
    itemsConfig.forEach((config, i) => {
      const { x, y, scale } = config;
      this._pumpkins[i].position.set(x, y);
      this._pumpkins[i].scale.set(scale);
    });
  }

  _build() {
    this._buildItems();
    CI_API.Globals.PARAMS.halloween_theme && this._buildPumpkins();
  }

  _buildItems() {
    const itemsConfig = getBackgroundItemsConfig();
    itemsConfig.forEach(config => {
      const { x, y, scale, type } = config;
      const item = makeImage(getItemImageConfig(type));
      item.position.set(x, y);
      item.scale.set(scale);

      this.addChild(item);

      this._items.push(item);
    });
  }

  _buildPumpkins() {
    const pumpkinsConfig = getPumpkinsConfig();
    pumpkinsConfig.forEach(config => {
      const { x, y, scale, type } = config;
      const pumpkin = makeImage(getPumpkinImageConfig(type));
      pumpkin.position.set(x, y);
      pumpkin.scale.set(scale);

      this.addChild(pumpkin);

      this._pumpkins.push(pumpkin);
    });
  }
}
