/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getForegroundGridConfig } from '../../configs/grid-configs';
import { AdStatus } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { postRunnable, tweenToCell } from '../../utils';
import { BlockerView } from './blocker-view';
import { HintView } from './hint-view';
import { LogoView } from './logo-view';
import { TutorialView } from './tutorial-view';

export class ForegroundView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    lego.event
      .on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this)
      .on(ModelEvents.AdModel.HintUpdate, this._onHintUpdate, this)
      .on(ModelEvents.AdModel.TutorialUpdate, this._onTutorialUpdate, this);
  }

  get name() {
    return 'ForegroundView';
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);

    if (this._tutorialHideTw) {
      this.game.tweens.remove(this._tutorialHideTw);
      this._tutorialHideTw = null;
      this._tutorialView && this._destroyTutorial();
    }

    if (this._tutorialBlockerHideTw) {
      this.game.tweens.remove(this._tutorialBlockerHideTw);
      this._tutorialBlockerHideTw = null;
      this._tutorialBlocker && this._destroyTutorialBlocker();
    }

    if (this._tutorialBlocker && !this._tutorialBlockerHideTw) {
      this._tutorialBlocker.destroy();
      this._tutorialBlocker = null;
      postRunnable(() => {
        this._buildBlocker();
        this._tutorialView && this.bringToTop(this._tutorialView);
        this._logoView && this.bringToTop(this._logoView);
      });
    }
  }

  getGridConfig() {
    return getForegroundGridConfig();
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Game:
        this._build();
        break;
      case AdStatus.PreCta:
        this.game.tweens.remove(this._tutorialHideTw);
        this.game.tweens.remove(this._tutorialBlockerHideTw);
        this._tutorialView && this._destroyTutorial();
        this._tutorialBlocker && this._destroyTutorialBlocker();
        break;
      case AdStatus.Cta:
        this._moveLogo();
        break;
      default:
        break;
    }
  }

  _build() {
    this._buildLogo();
  }

  // LOGO
  _buildLogo() {
    this.setChild('logo', (this._logoView = new LogoView()));
  }

  _moveLogo() {
    this._logoTw = tweenToCell(this, this._logoView, 'cta_logo', 500, Phaser.Easing.Sinusoidal.InOut, 0);
  }

  // TUTORIAL
  _onTutorialUpdate(tutorial) {
    if (tutorial) {
      this._buildTutorial();
    } else if (this._tutorialView) {
      this._tutorialHideTw = this._hideTutorial();
      this._tutorialHideTw.onComplete.add(() => this._destroyTutorial());
      this._tutorialHideTw.universal = true;

      this._tutorialBlockerHideTw = this._hideTutorialBlocker();
      this._tutorialBlockerHideTw.onComplete.add(() => this._destroyTutorialBlocker());
      this._tutorialBlockerHideTw.universal = true;
    }
  }

  _onTutorialSequenceReady() {
    this._buildBlocker();
    this.setChild('tutorial', this._tutorialView);
    this.bringToTop(this._logoView);
  }

  _buildTutorial() {
    this._tutorialView = new TutorialView();
    this._tutorialView.onSequenceReady.add(this._onTutorialSequenceReady, this);
  }

  _destroyTutorial() {
    this._tutorialView.destroy();
    this._tutorialView = null;
  }

  _hideTutorial() {
    const tw = this.game.add.tween(this._tutorialView).to({ alpha: 0 }, 500, Phaser.Easing.Sinusoidal.InOut, true);
    tw.universal = true;

    return tw;
  }

  _buildBlocker() {
    this.setChild('tutorial_blocker', (this._tutorialBlocker = new BlockerView()));
  }

  _destroyTutorialBlocker() {
    this._tutorialBlocker.destroy();
    this._tutorialBlocker = null;
  }

  _hideTutorialBlocker() {
    const tw = this.game.add.tween(this._tutorialBlocker).to({ alpha: 0 }, 500, Phaser.Easing.Sinusoidal.InOut, true);
    tw.universal = true;

    return tw;
  }

  // HINT
  _onHintUpdate(hint) {
    hint ? this._buildHint() : this._destroyHint();
  }

  _buildHint() {
    this.addChild((this._hintView = new HintView()));
  }

  _destroyHint() {
    this._hintView.destroy();
  }
}
