import {
  getCharacterBackImageConfig,
  getCharacterFrontImageConfig,
  getVeggiesImageConfig
} from '../../configs/image-configs';
import { makeImage } from '../../utils';
import { Container } from '../../utils/container';

export class CharacterComponent extends Container {
  constructor() {
    super();

    this._build();
  }

  lookForward() {
    this._frontImg.alpha = 0;
    this._backImg.alpha = 1;
    this._veggies.alpha = 1;
  }

  lookBackward() {
    this._frontImg.alpha = 1;
    this._backImg.alpha = 0;
    this._veggies.alpha = 0;
  }

  _build() {
    this._buildVeggies();
    this._buildFrontImg();
    this._buildBackImg();
  }

  _buildFrontImg() {
    this._frontImg = makeImage(getCharacterFrontImageConfig());

    this.addChild(this._frontImg);
  }

  _buildBackImg() {
    this._backImg = makeImage(getCharacterBackImageConfig());
    this._backImg.alpha = 0;

    this.addChild(this._backImg);
  }

  _buildVeggies() {
    this._veggies = makeImage(getVeggiesImageConfig());
    this._veggies.scale.set(0.8);
    this._veggies.alpha = 0;

    this.addChild(this._veggies);
  }
}
