/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getCTAButtonIconImageConfig } from '../../configs/image-configs';
import { getCtaPlayButtonPatchConfig } from '../../configs/ninepatch-configs';
import { getCtaPlayButtonTextConfig } from '../../configs/text-configs';
import { InputPriority } from '../../constants';
import { ViewEvents } from '../../events/view-events';
import { fitText, makeImage, makeNinePatch, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class CTAButtonComponent extends Container {
  constructor() {
    super();

    this._build();
  }

  _build() {
    const { cta_btn_style: style, cta_btn_icon: icon, cta_btn_text: text } = CI_API.Globals.PARAMS;
    const width = style === 'icon' ? 200 : 350;
    const height = 100;

    this._buildBg(width, height);
    style !== 'icon' && this._buildText(style, text);
    style !== 'text' && this._buildIcon(style, icon);

    this._buildHitArea();
  }

  _buildBg(width, height) {
    this._bg = makeNinePatch(getCtaPlayButtonPatchConfig(width, height));
    this.addChild(this._bg);
  }

  _buildText(style, text) {
    this._text = makeText(getCtaPlayButtonTextConfig(text));
    this._text.anchor.set(0.5);

    const percent = style === 'text' ? 0.8 : 0.6;
    const xOffset = style === 'text' ? 0 : 50;

    fitText(this._text, this._bg.width * percent, this._bg.height * percent);

    this._text.position.set(xOffset, 0);
    this._bg.addChild(this._text);
  }

  _buildIcon(style, icon) {
    this._icon = makeImage(getCTAButtonIconImageConfig(icon));
    this._icon.scale.set(0.8);

    const xOffset = style === 'icon' ? 0 : -115;

    this._icon.position.set(xOffset, 0);
    this._bg.addChild(this._icon);
  }

  _buildHitArea() {
    const { width, height } = this._bg;

    const gr = this.game.add.graphics();
    gr.beginFill(0xff0000, 0);
    gr.drawRoundedRect(-width / 2, -height / 2, width, height, 15);
    gr.endFill();

    gr.inputEnabled = true;
    gr.name = 'cta_play_button';
    gr.input.priorityID = InputPriority.Cta + 1;
    gr.events.onInputDown.add(this._onPlayClick, this);

    this.addChild((this._hitArea = gr));
  }

  _onPlayClick() {
    lego.event.emit(ViewEvents.CtaView.PlayClick);
  }
}
