import { lego } from '@armathai/lego';
import { CellState } from '../../constants';
import { TruckModel } from '../../models/game/board/truck-model';
import { store } from '../../models/store';
import { setCellStateCommand } from './set-cell-state-command';
import { setCellTruckCommand } from './set-cell-truck-command';

export function addNewTruckCommand() {
  const cell = store.game.boardModel.cells.find(c => !c.truck);
  const { uuid } = cell;

  lego.command
    //
    .payload(uuid, new TruckModel(1))
    .execute(setCellTruckCommand)

    .payload(uuid, CellState.Enable)
    .execute(setCellStateCommand);
}
