import { lego } from '@armathai/lego';
import {
  getButtonTruckConfig,
  getCoinImageConfig,
  getIdlePlusIconConfig,
  getPressedPlusIconConfig
} from '../../configs/image-configs';
import { getAddNewTruckButtonPatchConfig } from '../../configs/ninepatch-configs';
import { getAddNewTruckButtonTextConfig } from '../../configs/text-configs';
import { ViewEvents } from '../../events/view-events';
import { makeImage, makeNinePatch, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class EnvironmentButtonComponent extends Container {
  constructor() {
    super();

    this._build();
  }

  enable() {
    this._button.inputEnabled = true;
    this._button.tint = 0xffffff;
    this._text.tint = 0xffffff;
    this._coinImage.tint = 0xffffff;
    this._idlePlusIcon.tint = 0xffffff;
  }

  disable() {
    this._button.inputEnabled = false;
    this._button.tint = 0xacacac;
    this._text.tint = 0xacacac;
    this._coinImage.tint = 0xacacac;
    this._idlePlusIcon.tint = 0xacacac;
  }

  _build() {
    this._buildBg();
    this._buildTruckIcon();
    this._buildButton();
  }

  _buildBg() {
    const gr = this.game.add.graphics();
    gr.beginFill(0x203b46, 1);
    gr.drawRoundedRect(-50, -25, 100, 50, 10);
    gr.endFill();

    gr.position.set(-130, 0);

    this.addChild((this._buttonBg = gr));
  }

  _buildTruckIcon() {
    this._truckIcon = makeImage(getButtonTruckConfig());
    this._truckIcon.position.set(-5, 0);

    this._buttonBg.addChild(this._truckIcon);
  }

  _buildButton() {
    this._button = makeNinePatch(getAddNewTruckButtonPatchConfig());

    const text = makeText(getAddNewTruckButtonTextConfig());
    text.anchor.set(0.5);
    text.position.set(50, 0);
    this._button.addChild((this._text = text));

    const coinImage = makeImage(getCoinImageConfig());
    coinImage.scale.set(0.8);
    coinImage.position.set(-15, 0);
    this._button.addChild((this._coinImage = coinImage));

    const idlePlusIcon = makeImage(getIdlePlusIconConfig());
    idlePlusIcon.position.set(-65, 0);
    this._button.addChild((this._idlePlusIcon = idlePlusIcon));

    const pressedPlusIcon = makeImage(getPressedPlusIconConfig());
    pressedPlusIcon.position.set(-65, 0);
    pressedPlusIcon.alpha = 0;
    this._button.addChild((this._pressedPlusIcon = pressedPlusIcon));

    this._button.name = 'add_new_truck_button';
    this._button.events.onInputDown.add(this._onClick, this);

    this.addChild(this._button);
  }

  _onClick() {
    this._button.scale.set(1);
    const tw = this.game.add
      .tween(this._button.scale)
      .to({ x: 0.9, y: 0.9 }, 50, Phaser.Easing.Cubic.InOut, true, 0, 0, true);

    tw.onStart.add(() => {
      this._idlePlusIcon.alpha = 0;
      this._pressedPlusIcon.alpha = 1;
    });

    tw.onComplete.add(() => {
      this._idlePlusIcon.alpha = 1;
      this._pressedPlusIcon.alpha = 0;
    });

    lego.event.emit(ViewEvents.EnvironmentView.ButtonClick);
  }
}
