import { lego } from '@armathai/lego';
import { ButtonCost } from '../../constants';
import { goalCashReachedGuard } from '../../guards/game/goal-cash-reached-guard';
import { onGoalCashReachedCommand } from '../game/on-goal-cash-reached-command';
import { setAbleToUpgradeCommand } from './set-able-to-upgrade-command';

export function onCashUpdateCommand(newValue) {
  lego.command
    //
    .payload(newValue)
    .guard(goalCashReachedGuard)
    .execute(onGoalCashReachedCommand)

    .payload(true)
    .guard(() => newValue >= ButtonCost)
    .execute(setAbleToUpgradeCommand)

    .payload(false)
    .guard(() => !(newValue >= ButtonCost))
    .execute(setAbleToUpgradeCommand);
}
