import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCTAContainerGridConfig } from '../../configs/grid-configs';
import { AdStatus, GameOverReasons } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { postRunnable } from '../../utils';
import { CTAStandardLoseView } from './cta-standard-lose-view';
import { CTAStandardWinView } from './cta-standard-win-view';

/* eslint-disable class-methods-use-this */
export class CTAContainer extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  getGridConfig() {
    return getCTAContainerGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this._build();
        this._show();
        break;
      case AdStatus.Game:
        this._cta && this._destroyCta();
        break;
      default:
    }
  }

  // BUILD
  _build() {
    this.build(this.getGridConfig());
    this._cta && this._destroyCta();

    const { reason } = store.ad.cta;

    switch (reason) {
      case GameOverReasons.Idled:
      case GameOverReasons.Failed:
        this._buildStandardLoseCta();
        break;
      case GameOverReasons.ItemsToCtaReached:
        this._buildStandardWinCta();
        break;
      default:
        break;
    }
  }

  _destroyCta() {
    this._cta.destroy();
    this._cta = null;
  }

  _buildStandardWinCta() {
    this._cta = new CTAStandardWinView();
    this.setChild('cta', this._cta);
  }

  _buildStandardLoseCta() {
    this._cta = new CTAStandardLoseView();
    this.setChild('cta', this._cta);
  }

  // SHOW
  _show() {
    postRunnable(() => {
      this._cta.show();
    });
  }
}
