import { getInitialTrucksConfig } from '../../../configs/initial-trucks-config';
import { BoardState, CellState, TruckMaxLevel } from '../../../constants';
import { ObservableModel } from '../../observable-model';
import { CellModel } from './cell-model';
import { TruckModel } from './truck-model';

export class BoardModel extends ObservableModel {
  constructor() {
    super('BoardModel');

    this._cells = [];
    this._trucks = [];
    this._state = BoardState.Unknown;

    this.makeObservable();
  }

  get cells() {
    return this._cells;
  }

  get trucks() {
    return this._trucks;
  }

  set trucks(value) {
    this._trucks = value;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  getTruckByUuid(uuid) {
    return this._trucks.find(t => t.uuid === uuid);
  }

  getCellByUuid(uuid) {
    return this._cells.find(c => c.uuid === uuid);
  }

  getCellByState(state) {
    return this._cells.find(c => c.state === state);
  }

  getCellByPosition(row, column) {
    return this._cells.find(c => c.row === row && c.column === column);
  }

  getCellsForHint() {
    const cells = { fromCellUuid: null, toCellUuid: null };
    const cellsWithTrucks = this._cells.filter(c => c.truck);

    if (cellsWithTrucks.length <= 1) {
      return cells;
    }

    for (let i = 0; i < cellsWithTrucks.length; i += 1) {
      const fromCell = cellsWithTrucks[i];
      const { level: fromCellTruckLvl } = fromCell.truck;

      for (let j = 0; j < cellsWithTrucks.length; j += 1) {
        const toCell = cellsWithTrucks[j];
        const { level: toCellTruckLvl } = toCell.truck;

        if (
          i !== j &&
          fromCellTruckLvl === toCellTruckLvl &&
          fromCellTruckLvl !== TruckMaxLevel &&
          toCellTruckLvl !== TruckMaxLevel
        ) {
          cells.fromCellUuid = fromCell.uuid;
          cells.toCellUuid = toCell.uuid;

          return cells;
        }
      }
    }

    return cells;
  }

  initialize() {
    this._state = BoardState.Idle;

    this._initializeCells();
    this._initializeInitialTrucks();
  }

  _initializeCells() {
    const row = 3;
    const column = 3;

    const arr = [];

    for (let i = 0; i < row; i += 1) {
      for (let j = 0; j < column; j += 1) {
        const cell = new CellModel(i, j);

        arr.push(cell);
      }
    }

    this._cells = [...arr];
  }

  _initializeInitialTrucks() {
    const initialConfigs = getInitialTrucksConfig();

    initialConfigs.forEach(config => {
      const { row, column, level } = config;
      const cell = this.getCellByPosition(row, column);

      cell.truck = new TruckModel(level);
      cell.state = CellState.Enable;
    });
  }
}
