/* eslint-disable class-methods-use-this */
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getGameGridConfig } from '../../configs/grid-configs';
import { EnvironmentView } from './environment-view';

export class GameView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    this._environment = null;

    this._build();
  }

  get name() {
    return 'GameView';
  }

  getGridConfig() {
    return getGameGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    this._environment && this._environment.rebuild();

    super.rebuild(config);
  }

  _build() {
    this._buildEnvironment();
  }

  _buildEnvironment() {
    this._environment = new EnvironmentView();
    this.setChild('environment', this._environment);
  }
}
