import {
  getHouseBridgeImageConfig,
  getHouseImageConfig,
  getHouseUpperPartImageConfig
} from '../../configs/image-configs';
import { getPointsTextConfig } from '../../configs/text-configs';
import { fitText, makeImage, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class HouseComponent extends Container {
  constructor() {
    super();

    this._build();
  }

  updatePointsLabel(points) {
    this._destroyPointsLabel();
    this._buildPointsLabel(points);
  }

  _build() {
    this._buildHouse();
    this._buildBridge();
    this._buildUpperPart();

    this._buildPointsLabel();
  }

  _buildHouse() {
    this._house = makeImage(getHouseImageConfig());

    this.addChild(this._house);
  }

  _buildUpperPart() {
    this._upperPart = makeImage(getHouseUpperPartImageConfig());
    this._upperPart.position.set(0, this._house.top - 18);

    this.addChild(this._upperPart);
  }

  _buildBridge() {
    this._bridge = makeImage(getHouseBridgeImageConfig());
    this._bridge.position.set(0, this._house.top - 50);

    this.addChild(this._bridge);
  }

  _buildPointsLabel(points = 0) {
    this._pointsLabel = makeText(getPointsTextConfig(points));
    this._pointsLabel.anchor.set(0.5);

    fitText(this._pointsLabel, 70, 200);

    this.addChild(this._pointsLabel);
  }

  _destroyPointsLabel() {
    this._pointsLabel.destroy();
    this._pointsLabel = null;
  }
}
