/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getCTAStandardGridConfig } from '../../configs/grid-configs';
import { getOverlayPatchConfig } from '../../configs/ninepatch-configs';
import { ViewEvents } from '../../events/view-events';
import { makeNinePatch, postRunnable } from '../../utils';
import { CTAView } from './cta-view';
import { WinCtaContentComponent } from './win-cta-content-component';

export class CTAStandardWinView extends CTAView {
  constructor() {
    super();

    this._build();
    this._show();
  }

  getGridConfig() {
    return getCTAStandardGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  // BUILD
  _build() {
    super.build(this.getGridConfig());

    this._buildOverlay();
    this._buildContent();
  }

  _buildOverlay() {
    this._overlay = makeNinePatch(getOverlayPatchConfig());
    this._overlay.alpha = 0;

    this.setChild('overlay', this._overlay);
    this.bringToTop(this._playBtn);
  }

  _buildContent() {
    this._content = new WinCtaContentComponent();
    this._content.alpha = 0;

    this.setChild('content', this._content);
  }

  // SHOW
  _show() {
    postRunnable(() => {
      this._showOverlay();
      this._showContent();
    });
  }

  _showOverlay() {
    this._showOverlayTw = this.game.add
      .tween(this._overlay)
      .to({ alpha: 1 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0);

    return this._showOverlayTw;
  }

  _showContent() {
    this._showContentTw = this.game.add
      .tween(this._content.scale)
      .from({ x: 0, y: 0 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0);
    this._showContentTw.onStart.add(() => {
      this._content.alpha = 1;
    });
    this._showContentTw.onComplete.add(() => {
      lego.event.emit(ViewEvents.CtaView.ContentShowComplete);
    });

    return this._showContentTw;
  }
}
