import { lego } from '@armathai/lego';
import { PointsMultiplier } from '../../constants';
import { startTruckMovementCommand } from '../game/start-truck-movement-command';
import { creditPlayerPointsCommand } from '../player/credit-player-points-command';

export function onCharacterReachedCommand() {
  lego.command
    //
    .payload(PointsMultiplier)
    .execute(creditPlayerPointsCommand)

    .execute(startTruckMovementCommand);
}
