import { lego } from '@armathai/lego';
import { AdStatus } from '../constants';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';

let sfx;
let gameTheme;

function play(id, volume = 1) {
  sfx.play(id, volume);
}

// function stop(id) {
//   sfx.stop(id);
// }

function playGameTheme() {
  soundLoader.play(gameTheme);
}

// function stopGameTheme() {
//   gameTheme.fadeTo(500, 0);
//   gameTheme.onFadeComplete.add(() => {
//     gameTheme.stop();
//   });
// }

function onLoadCompleteUpdate(complete) {
  if (!complete) {
    return;
  }

  sfx = soundLoader.addAudioSprite('audio');
  gameTheme = CI_API.game.add.audio('game-theme');
  gameTheme.loop = true;
  gameTheme.volume = 0.5;

  playGameTheme();
}

function onAdStatusUpdate(status) {
  switch (status) {
    case AdStatus.Cta:
      break;
    default:
      break;
  }
}

function onButtonClick() {
  play('click');
}

function onSequenceShowComplete(index) {
  switch (index) {
    case 0:
      play('popup');
      break;
    default:
      break;
  }
}

function onCtaContentShowComplete() {
  play('popup');
}

function onTruckReachedPillar() {
  play('coins');
}

export function SoundObservant() {
  lego.event
    .on(ModelEvents.LoadModel.CompleteUpdate, onLoadCompleteUpdate, this)
    .on(ModelEvents.AdModel.StatusUpdate, onAdStatusUpdate, this)
    .on(ViewEvents.EnvironmentView.ButtonClick, onButtonClick, this)
    .on(ViewEvents.TutorialView.SequenceShowComplete, onSequenceShowComplete, this)
    .on(ViewEvents.CtaView.ContentShowComplete, onCtaContentShowComplete, this)
    .on(ViewEvents.RoadView.TruckReachedPillar, onTruckReachedPillar, this);
}
