import { lego, not } from '@armathai/lego';
import { hintModelGuard } from '../guards/ad/hint-model-guard';
import { hintVisibleGuard } from '../guards/ad/hint-visible-guard';
import { draggableCellExistGuard } from '../guards/board/draggable-cell-exist-guard';
import { hideHintCommand } from './ad/hint/hide-hint-command';
import { startHintVisibilityTimerCommand } from './ad/hint/start-hint-visibility-timer-command';
import { stopHintVisibilityTimerCommand } from './ad/hint/stop-hint-visibility-timer-command';

export function resizeCommand() {
  lego.command
    //
    .guard(hintModelGuard, hintVisibleGuard, not(draggableCellExistGuard))
    .execute(hideHintCommand, stopHintVisibilityTimerCommand, startHintVisibilityTimerCommand);
}
