/* eslint-disable class-methods-use-this */
import { getCharacterImageConfig, getGlowImageConfig, getWellDoneImageConfig } from '../../configs/image-configs';
import { makeImage } from '../../utils';
import { Container } from '../../utils/container';

export class WinCtaContentComponent extends Container {
  constructor() {
    super();

    this._build();
  }

  getBounds() {
    return new Phaser.Rectangle(-275, -250, 550, 500);
  }

  _build() {
    this._buildGlow();
    this._buildCharacter();
    this._buildLabel();
  }

  _buildGlow() {
    this._glow = makeImage(getGlowImageConfig());

    const tw = this.game.add.tween(this._glow).to({ angle: 360 }, 20000, Phaser.Easing.Linear.None, true, 0, -1, false);
    tw.universal = true;

    this.addChild(this._glow);
  }

  _buildCharacter() {
    this._character = makeImage(getCharacterImageConfig());

    this.addChild(this._character);
  }

  _buildLabel() {
    this._label = makeImage(getWellDoneImageConfig());
    this._label.position.set(0, this._character.bottom);

    this.addChild(this._label);
  }
}
