import { lego } from '@armathai/lego';
import { CellState, TruckMaxLevel } from '../../constants';
import { store } from '../../models/store';
import { enableRestCellsCommand } from '../board/enable-rest-cells-command';
import { setCellStateCommand } from '../board/set-cell-state-command';

export function onCellClickCommand(fromCellUuid) {
  const { level } = store.game.boardModel.getCellByUuid(fromCellUuid).truck;
  const cellsWithTrucks = store.game.boardModel.cells.filter(c => c.truck);
  const cellWithSameTruckLevel = cellsWithTrucks.find(
    c => c.uuid !== fromCellUuid && c.truck.level === level && c.truck.level !== TruckMaxLevel
  );

  if (cellWithSameTruckLevel) {
    const { uuid: toCellUuid } = cellWithSameTruckLevel;

    lego.command
      //
      .payload(fromCellUuid, CellState.FromCell)
      .execute(setCellStateCommand)

      .payload(toCellUuid, CellState.ToCell)
      .execute(setCellStateCommand);
  } else {
    lego.command
      //
      .execute(enableRestCellsCommand);
  }
}
