/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getCashIconImageConfig } from '../../configs/image-configs';
import { getTopBarElementBgPatchConfig } from '../../configs/ninepatch-configs';
import { getTopBarElementTextConfig } from '../../configs/text-configs';
import { ModelEvents } from '../../events/model-events';
import { fitText, makeImage, makeNinePatch, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class CashView extends Container {
  constructor() {
    super();

    lego.event.on(ModelEvents.PlayerModel.CashUpdate, this._onCashUpdate, this);

    this._build();
  }

  get name() {
    return 'CashView';
  }

  getBounds() {
    return this._bg.getBounds();
  }

  getCashPos() {
    return this.toGlobal(new Phaser.Point(this._cashIcon.centerX, this._cashIcon.centerY));
  }

  _onCashUpdate(newAmount) {
    if (this._text) {
      this._text.destroy();
      this._buildText(newAmount);
    }
  }

  _build() {
    this._buildBg();
    this._buildText();
    this._buildCashIcon();
  }

  _buildBg() {
    this._bg = makeNinePatch(getTopBarElementBgPatchConfig());
    this.addChild(this._bg);
  }

  _buildText(money = 0) {
    const { width, height } = this._bg;

    this._text = makeText(getTopBarElementTextConfig(money));
    this._text.anchor.set(0.5);

    fitText(this._text, width * 0.6, height * 0.8);

    this.addChild(this._text);
  }

  _buildCashIcon() {
    this._cashIcon = makeImage(getCashIconImageConfig());
    this._cashIcon.position.set(this.left, 0);

    this.addChild(this._cashIcon);
  }
}
