import { ObservableModel } from '../observable-model';

export class PlayerModel extends ObservableModel {
  constructor() {
    super('PlayerModel');

    this._cash = null;
    this._points = null;
    this._ableToUpgrade = null;

    this.makeObservable();
  }

  get cash() {
    return this._cash;
  }

  set cash(value) {
    this._cash = value;
  }

  get points() {
    return this._points;
  }

  set points(value) {
    this._points = Math.max(0, value);
  }

  get ableToUpgrade() {
    return this._ableToUpgrade;
  }

  set ableToUpgrade(value) {
    this._ableToUpgrade = value;
  }

  initialize() {
    this._cash = 0;
    this._points = 0;
  }
}
