/* eslint-disable class-methods-use-this */
import { InputPriority } from '../../constants';
import LU from '../../display/layout-utils';
import { getViewByProperty, isSquareLikeScreen, makeImage, postRunnable } from '../../utils/index';

export class SoundIconBuilder {
  constructor() {
    this._onBtn = null;

    this._build();
  }

  onResize() {
    this._setPosition();
  }

  _build() {
    this._onBtn = makeImage({ frame: 'sound_on.png' });
    soundLoader.registerMuteButton(this._onBtn);

    this._onBtn.name = 'mute_button';
    this._onBtn.input.priorityID = InputPriority.Foreground;
    CI_API.game.world.add(this._onBtn);

    postRunnable(() => {
      LU.resizeCall(CI_API.game.world);
    });
  }

  _setPosition() {
    postRunnable(() => {
      const uiView = getViewByProperty('name', 'UIView');
      const topBarPos = uiView.getTopBarPosition();

      this._onBtn.x = this._onBtn.width * 0.5 + LP(isSquareLikeScreen() ? 15 : 75, 25);
      this._onBtn.y = topBarPos.y + (isSquareLikeScreen() ? 125 : 125);
    });
  }
}
