import { lego, not } from '@armathai/lego';
import { lastTutorialSequenceGuard } from '../../../guards/ad/tutorial-last-sequence-guard';
import { completeTutorialSequenceCommand } from './complete-tutorial-sequence-command';
import { setTutorialSkipCommand } from './set-tutorial-skip-command';

export function tutorialScreenClickCommand() {
  lego.command
    //
    .guard(not(lastTutorialSequenceGuard))
    .execute(completeTutorialSequenceCommand)

    .guard(lastTutorialSequenceGuard)
    .execute(setTutorialSkipCommand);
}
