import { lego } from '@armathai/lego';
import { CashMultiplier } from '../../constants';
import { debitPlayerPointsCommand } from '../player/debit-player-points-command';

export function onTruckTakeVeggiesCommand(level) {
  lego.command
    //
    .payload(level * CashMultiplier)
    .execute(debitPlayerPointsCommand);
}
