import { lego } from '@armathai/lego';
import { ButtonCost } from '../../constants';
import { emptyCellExistGuard } from '../../guards/board/empty-cell-exist-guard';
import { addNewTruckCommand } from '../board/add-new-truck-command';
import { debitPlayerCashCommand } from '../player/debit-player-cash-command';

export function onAddNewTruckButtonClickCommand() {
  lego.command
    //
    .payload(ButtonCost)
    .guard(emptyCellExistGuard)
    .execute(addNewTruckCommand, debitPlayerCashCommand);
}
