/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getStreetImageConfig } from '../../configs/image-configs';
import { ViewEvents } from '../../events/view-events';
import { makeImage } from '../../utils';
import { Container } from '../../utils/container';
import { CharacterComponent } from './character-component';
import { HouseComponent } from './house-component';

export class StreetComponent extends Container {
  constructor() {
    super();

    this._build();
  }

  get name() {
    return 'StreetComponent';
  }

  getHousePosition() {
    return this.toGlobal(this._house.position);
  }

  updatePoints(points) {
    this._house.updatePointsLabel(points);
  }

  startCharacterMovement() {
    this._forwardTween = this.game.add
      .tween(this._character)
      .to({ y: this._house.bottom }, 500, Phaser.Easing.Linear.None, false, 0);

    this._backwardTween = this.game.add
      .tween(this._character)
      .to({ y: this._street.bottom - 500 }, 500, Phaser.Easing.Linear.None, false, 0);

    this._forwardTween.onComplete.add(() => {
      this._backwardTween.start();
      this._character.lookBackward();

      lego.event.emit(ViewEvents.EnvironmentView.CharacterReached);
    });

    this._backwardTween.onComplete.add(() => {
      this._forwardTween.start();
      this._character.lookForward();
    });

    this._forwardTween.universal = true;
    this._backwardTween.universal = true;

    this._backwardTween.start();
  }

  stopCharacterMovement() {
    this.game.tweens.remove(this._forwardTween);
    this.game.tweens.remove(this._backwardTween);
  }

  // BUILD
  _build() {
    this._buildStreet();
    this._buildHouse();
    this._buildCharacter();
  }

  _buildStreet() {
    this._street = makeImage(getStreetImageConfig());
    this._street.anchor.set(0.225, 1);

    this.addChild(this._street);
  }

  _buildHouse() {
    this._house = new HouseComponent();
    this._house.position.set(this._street.x, this._street.top);

    this.addChild(this._house);
  }

  _buildCharacter() {
    this._character = new CharacterComponent();
    this._character.position.set(this._street.x, this._house.bottom);

    this.addChild(this._character);
  }
}
