import { CellState } from '../../constants';
import { store } from '../../models/store';

export function returnAndDisableCellsCommand() {
  store.game.boardModel.cells.forEach(c => {
    if (c.state === CellState.Drag) {
      c.state = CellState.Return;
    } else if (c.state === CellState.Enable) {
      c.state = CellState.Disable;
    }
  });
}
