import { getGameBounds, getViewByProperty, postRunnable } from '../../utils';
import { Container } from '../../utils/container';

export class BlockerView extends Container {
  constructor() {
    super();

    this._build();
  }

  show() {
    this.alpha = 0;
  }

  _build() {
    this._buildCutBlocker();
  }

  _buildCutBlocker() {
    const { x, y, width, height } = getGameBounds();
    const bmd = this.game.add.bitmapData(width, height);
    const blocker = this.game.add.sprite(x, y, bmd);
    this._ctx = bmd.context;
    this._bmd = bmd;
    this.addChild((this._cutBlocker = blocker));
    this._fillBmd(width, height);
    postRunnable(() => {
      this._cutBoard();
    });
    blocker.alpha = 0.5;
  }

  _fillBmd(width, height) {
    this._ctx.beginPath();
    this._ctx.globalCompositeOperation = 'source-over';
    this._ctx.fillStyle = 'rgba(0,0,0,1)';
    this._ctx.rect(0, 0, width, height);
    this._ctx.fill();
    this._ctx.closePath();
  }

  _cutBoard() {
    const environmentView = getViewByProperty('name', 'EnvironmentView');
    const boardView = getViewByProperty('name', 'BoardView');
    const { x, y } = this.toLocal(environmentView.getBoardPosition(), undefined);
    const rectangle = boardView.getBounds();
    this._ctx.globalCompositeOperation = 'destination-out';
    this._ctx.fillStyle = 'rgba(255,255,255,1)';
    this._ctx.fillRect(x - rectangle.width / 2, y - rectangle.height, rectangle.width, rectangle.height);
  }
}
