/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getPillarImageConfig } from '../../configs/image-configs';
import { getFencePatchConfig } from '../../configs/ninepatch-configs';
import { AdStatus, BoardState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { makeImage, makeNinePatch } from '../../utils';
import { Container } from '../../utils/container';
import { BackgroundItemsComponent } from './background-items-component';
import { BoardView } from './board/board-view';
import { EnvironmentButtonComponent } from './environment-button-component';
import { RoadView } from './road-view';
import { StreetComponent } from './street-component';

export class EnvironmentView extends Container {
  constructor() {
    super();

    this._boardView = null;

    lego.event
      .on(ModelEvents.GameModel.BoardModelUpdate, this._onBoardUpdate, this)
      .on(ModelEvents.BoardModel.StateUpdate, this._onBoardStateUpdate, this)
      .on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this)
      .on(ModelEvents.PlayerModel.PointsUpdate, this._onPlayerPointsUpdate, this)
      .on(ModelEvents.PlayerModel.AbleToUpgradeUpdate, this._onPlayerAbleToUpgradeUpdate, this);

    this._build();
  }

  get name() {
    return 'EnvironmentView';
  }

  getBounds() {
    return new Phaser.Rectangle(...LP([-500, -400, 1000, 800], [-300, -500, 600, 1000]));
  }

  getBoardPosition() {
    return this.toGlobal(this._boardView.position);
  }

  getButtonPosition() {
    return this.toGlobal(this._button.position);
  }

  getPillarPosition() {
    return this.toGlobal(this._pillar.position);
  }

  rebuild() {
    this._buildBounds();

    this._repositionElements();

    this._bgItems && this._bgItems.reposition();
    this._road && this._road.rebuild();
  }

  _repositionElements() {
    this._road.position.set(0, LP(-110, -210));
    this._leftFence.position.set(this.left + LP(200, -10), LP(-110, -210));
    this._rightFence.position.set(this.right + LP(-100, 10), LP(-110, -210));
    this._pillar.position.set(this.right - LP(125, 25), LP(-50, LP(-150, -150)));
    this._street.position.set(LP(-225, -220), this.bottom + LP(350, 150));

    this._button.position.set(LP(110, 110), LP(-50, -100));

    this._boardView && this._boardView.position.set(LP(75, 75), this.bottom - LP(10, 150));
  }

  // AD
  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.PreCta:
        this._street.stopCharacterMovement();
        break;
      default:
        break;
    }
  }

  // POINTS
  _onPlayerPointsUpdate(points) {
    this._street.updatePoints(points);
  }

  // AbleToUpgrade
  _onPlayerAbleToUpgradeUpdate(ableToUpgrade) {
    ableToUpgrade ? this._button.enable() : this._button.disable();
  }

  // BOARD
  _onBoardUpdate(board) {
    board && this._buildBoard();
  }

  _onBoardStateUpdate(state) {
    switch (state) {
      case BoardState.Game:
        this._street.startCharacterMovement();
        break;
      default:
        break;
    }
  }

  _buildBoard() {
    this._boardView = new BoardView();
    this._boardView.position.set(LP(75, 75), this.bottom - LP(10, 150));

    this.addChild(this._boardView);
  }

  _destroyBoard() {
    this._boardView.destroy();
    this._boardView = null;
  }

  // BUILD
  _build() {
    this._buildBounds();
    this._buildRoad();
    this._buildLeftFence();
    this._buildRightFence();
    this._buildPillar();
    this._buildStreet();

    this._buildButton();
    this._buildBackgroundItems();

    this._repositionElements();
  }

  _buildBounds() {
    this._boundsRect && this._boundsRect.destroy();

    const { x, y, width, height } = this.getBounds();
    const gr = this.game.add.graphics();
    gr.beginFill(0xff0000, 0);
    gr.drawRect(x, y, width, height);
    gr.endFill();

    this.addChild((this._boundsRect = gr));
  }

  _buildRoad() {
    this._road = new RoadView();
    this.addChild(this._road);
  }

  _buildLeftFence() {
    this._leftFence = makeNinePatch(getFencePatchConfig());
    this._leftFence.anchor.set(1, 0.5);
    this.addChild(this._leftFence);
  }

  _buildRightFence() {
    this._rightFence = makeNinePatch(getFencePatchConfig());
    this._rightFence.anchor.set(0, 0.5);
    this.addChild(this._rightFence);
  }

  _buildPillar() {
    this._pillar = makeImage(getPillarImageConfig());
    this._pillar.anchor.set(0.5, 1);
    this.addChild(this._pillar);
  }

  _buildStreet() {
    this._street = new StreetComponent();
    this.addChild(this._street);
  }

  _buildButton() {
    this._button = new EnvironmentButtonComponent();
    this.addChild(this._button);
  }

  _buildBackgroundItems() {
    this._bgItems = new BackgroundItemsComponent();
    this.addChild(this._bgItems);
  }
}
