/* eslint-disable class-methods-use-this */
import { getCharacterImageConfig } from '../../configs/image-configs';
import { getBubblePatchConfig } from '../../configs/ninepatch-configs';
import { getTutorialTextConfig } from '../../configs/text-configs';
import { fitText, makeImage, makeNinePatch, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class LoseCtaContentComponent extends Container {
  constructor() {
    super();

    this._build();
  }

  getBounds() {
    return new Phaser.Rectangle(-275, -250, 550, 500);
  }

  _build() {
    this._buildCharacter();
    this._buildPopup();
  }

  _buildCharacter() {
    this._character = makeImage(getCharacterImageConfig());
    this._character.position.set(0, -50);

    this.addChild(this._character);
  }

  _buildPopup() {
    this._popup = makeNinePatch(getBubblePatchConfig());
    const text = makeText(getTutorialTextConfig('Keep Farming!'));

    text.anchor.set(0.5);
    fitText(text, this._popup.width * 0.8, this._popup.height * 0.8);

    this._popup.addChild(text);
    this._popup.position.set(0, this._character.bottom);

    this.addChild(this._popup);
  }
}
